@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$font-family-en: "Roboto", sans-serif;
$font-family-en2: "Lato", sans-serif;
$font-family-jp: "Noto Sans JP", sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $font-family-jp;
}

// フォントウェイト
$regular: 300;
$normal: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$white: #fff;
$blue: #004165;
$yellow:#FFD24E;
$dark-yellow:#2D2407;
$black: #000000;
