.pagenavi .wp-pagenavi .previouspostslink span,
.pagenavi .wp-pagenavi .nextpostslink span {
  position: relative;
  display: block;
  width: 0.725rem;
  height: 1.3875rem;
}

.pagenavi .wp-pagenavi .previouspostslink span::before,
.pagenavi .wp-pagenavi .previouspostslink span::after,
.pagenavi .wp-pagenavi .nextpostslink span::before,
.pagenavi .wp-pagenavi .nextpostslink span::after {
  position: absolute;
  content: "";
