  left: 0;
  transform: rotate(45deg);
  transform-origin: 0.03125rem 50%;
}

.pagenavi .wp-pagenavi .previouspostslink span::after {
  left: 0;
  transform: rotate(-45deg);
  transform-origin: 0.03125rem 50%;
}

