@use "global" as *;

.contact-form__wrap {
  padding-bottom: rem(24);
  border-bottom: 1px dotted $blue;
  @include mq("md") {
    padding-bottom: rem(40);
  }
}

.contact-form__wrap:not(:first-child) {
  margin-top: rem(21);
  @include mq("md") {
    margin-top: rem(40);
  }
}

.contact-form__label-wrap {
  @include mq("md") {
    display: flex;
  }
}

.contact-form__label {
  font-size: rem(16);
  font-weight: bold;
  font-family: $font-family-jp;
  color: $blue;
  @include mq("md") {
    max-width: rem(218);
    width: 100%;
  }
}

.contact-form__label span {
  font-size: rem(12);
  font-weight: normal;
  color: $white;
  background-color: #c94800;
  padding: rem(2) rem(13.5);
  display: inline-block;
  margin-left: rem(8);
  letter-spacing: 0.4em;
}

.contact-form__select {
  margin-top: rem(16);
  flex-grow: 1;

  @include mq("md") {
    margin-top: rem(0);
  }
}

.contact-form__input {
  width: 100%;
  margin-top: rem(16);
  flex-grow: 1;
  @include mq("md") {
    margin-top: rem(0);
  }
}

.contact-form__input input {
   border: 1px solid $blue;
  padding: rem(8) rem(16) rem(8) rem(16);
  width: 100%;
}

.contact-form__input input.wpcf7-not-valid  {
  border: 1px solid #c94800;
}

.contact-form__input input::placeholder {
  color: #c0e7fb;
}

.contact-form__checkbox {
  margin-top: rem(12);
  @include mq("md") {
    margin-top: rem(0);
  }
}

.contact-form__checkbox label:not(:first-child) {
  margin-top: rem(4);
}

.contact-form__checkbox input + span {
  position: relative;
  display: inline-block;
  padding-left: rem(22);
  font-size: rem(16);
}

.contact-form__checkbox input + span::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid $blue;
  display: block;
  width: rem(14);
  height: rem(14);
}

.contact-form__checkbox input + span::after {
  content: "";
  margin-top: rem(-2);
  position: absolute;
  top: 55%;
  left: rem(2);
  transform: translateY(-50%) rotate(-45deg);
  display: block;
  width: rem(9);
  height: rem(5);
  border-bottom: 2px solid #c94800;
  border-left: 2px solid #c94800;
  transition: 0.3s;
  opacity: 0;
}

.contact-form__checkbox input:checked + span::after {
  opacity: 1;
}

.contact-form__textarea {
  width: 100%;
  height: rem(200);
  margin-top: rem(16);
  @include mq("md") {
    margin-top: rem(0);
  }
}

.contact-form__textarea textarea {
  border: 1px solid $blue;
  padding: rem(8) rem(10);
  width: 100%;
  height: 100%;
}

.contact-form__textarea textarea.wpcf7-not-valid {
  border: 1px solid #c94800;
}

.contact-form__privacy {
  margin-top: rem(34);
  text-align: center;
  position: relative;
  @include mq("md") {
    margin-top: rem(79);
  }
}

.contact-form__privacy-label {
  display: block;
  max-width: rem(282);
  width: 100%;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  @include mq("md") {
    max-width: 100%;
  }
}

.contact-form__privacy-label input + span {
  position: relative;
  padding-left: rem(29);
  display: inline-block;
  font-size: rem(14);
  text-align: left;
}

.contact-form__privacy-label input + span::before {
  position: absolute;
  content: "";
  background: $white;
  border: 1px solid $blue;
  content: "";
  display: block;
  left: 0;
  top: rem(5);
  width: rem(22);
  height: rem(22);
  @include mq("md") {
    top: rem(0);
  }
}

.contact-form__privacy-label input + span::after {
  content: "";
  margin-top: rem(-2);
  position: absolute;
  top: rem(12);
  left: rem(2);
  transform: rotate(-45deg);
  display: block;
  width: rem(16);
  height: rem(7);
  border-bottom: 2px solid #c94800;
  border-left: 2px solid #c94800;
  transition: 0.3s;
  opacity: 0;
  @include mq("md") {
    top: rem(5);
  }
}

.contact-form__privacy-label input:checked + span::after {
  opacity: 1;
}

.wpcf7-form-control {
display: grid;
}

.contact-form__button {
  margin-top: rem(33);
  text-align: center;
}



.wpcf7-form .wpcf7-not-valid {
  background-color: #ffedee; /* 薄い赤色 */
}

.wpcf7-form .wpcf7-not-valid textarea {
  border-color: red; /* ボーダーの色を赤に */
  padding: 0; /* パディングを0に */
  background-color: #ffedee; /* 背景色を薄い赤に */
  width: 100%;
  height: 100%;
}

.wpcf7-form .wpcf7-not-valid .contact-form__textarea {
  padding: 0; /* パディングを0に */
}

.wpcf7-form .wpcf7-not-valid::placeholder {
  color: $white;
}

.wpcf7-form {
  position: relative;
  padding-top: 30px; /* エラーメッセージ用のスペース */
}


.wpcf7-response-output {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: #c94800;
  font-size: 14px;
  text-align: center;
}

/* 個別のエラーメッセージを非表示にする */
.wpcf7-form .wpcf7-not-valid-tip {
  display: none;
}

.wpcf7 form .wpcf7-response-output {
  border: none;
}



//ボタン
.contact-form__button .wpcf7-spinner {
  display: none !important;
}

.contact-form__button .button input[type="submit"] {
  color: $blue;
  transition: color 0.3s ease;
  position: relative;
  left: rem(46);
}






